html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
  color: #000;
  line-height: 1;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3, .enlace {
  margin: 0 0 5rem 0;
}

h1, .enlace {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 65%;
  height: auto;
  display: flex;
  margin: 0 auto;
}

.header {
  background-color: #4cb8b3;
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  box-shadow: 0.1rem 0.2rem 0.1rem #333;
  z-index: 2;
}

.navegacion-principal {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.logo {
  width: 4rem;
  margin: 0;
}

.nav-list {
  list-style-type: none;
  display: flex;
  gap: 2rem;
}

.nav-list li a {
  text-decoration: none;
  color: #FFF;
}

.nav-list li a:hover {
  color: #fef46d;
}

.abrir-menu,
.cerrar-menu {
  display: none;
}

@media screen and (max-width: 768px) {
  .abrir-menu,
  .cerrar-menu {
    color: #FFF;
    display: block;
    border: 0;
    font-size: 2.25rem;
    background-color: transparent;
    cursor: pointer;
  }
  .nav {
    visibility: hidden;
    opacity: 0;
    display: flex;
    flex-direction: column;
    align-items: end;
    gap: 2rem;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #4cb8b3;
    padding: 2rem;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
  }
  .nav.visible {
    opacity: 1;
    visibility: visible;
  }
  .nav-list {
    flex-direction: column;
    align-items: end;
  }
}
