.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(125, 3, 255);
  background: linear-gradient(90deg, rgba(125, 3, 255, 0.25) 0%, rgba(255, 0, 110, 0.3) 100%, rgba(255, 0, 110, 0.3177570093) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #FFF;
  font-size: 7;
  margin: 0;
}
.contenido-video p {
  background-color: #f53756;
  display: inline-block;
  color: #FFF;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.sobre-mi {
  padding: 8rem 0;
}
@media (min-width: 768px) {
  .sobre-mi {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1fr;
    align-items: center;
  }
}

.button {
  display: inline-block;
  background-color: #f53756;
  color: #FFF;
  width: max-content;
  padding: 0.75rem 2.5rem;
  font-weight: 600;
  border-radius: 0.5rem;
}

.button:hover {
  box-shadow: 0 10px 36px rgba(0, 0, 0, 0.15);
}

.iconos-sobremi {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 2rem;
}

.icon-sobremi {
  font-size: 3.5rem;
  color: #000;
}

.icon-sobremi:hover {
  color: #4cb8b3;
}

.contenido-sobremi {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-sobremi {
    margin-top: 0;
  }
}
.contenido-sobremi h2 {
  margin-bottom: 2rem;
}
.contenido-sobremi .name {
  color: #f53756;
  font-weight: 700;
}

.skills {
  padding: 8rem 4rem;
}
.skills h3 {
  text-align: center;
}

.lenguajes-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 480px) {
  .lenguajes-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .lenguajes-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 10rem;
    align-items: center;
  }
}

.lenguajes-imagenes img:hover {
  transform: scale(1.3);
}

.frameworks-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 480px) {
  .frameworks-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .frameworks-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10rem;
    align-items: center;
  }
}

.tecnologias-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 480px) {
  .tecnologias-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1rem;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .tecnologias-imagenes {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 7rem;
    align-items: center;
  }
}

.lenguajes-imagenes img:hover,
.frameworks-imagenes img:hover,
.tecnologias-imagenes img:hover {
  transform: scale(1.4);
}
@media (min-width: 480px) {
  .lenguajes-imagenes img:hover,
  .frameworks-imagenes img:hover,
  .tecnologias-imagenes img:hover {
    transform: scale(1.3);
  }
}

.proyectos {
  padding: 8rem 0;
  text-align: center;
}

.proyectos-enlaces {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .proyectos-enlaces {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
  }
}

.proyectos-contenedor {
  row-gap: 2rem;
}

.proyectos-links {
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
  border-radius: 0.55rem;
  overflow: hidden;
  width: 350px;
  height: 350px;
  margin: 0 auto;
}

.proyectos-links img {
  transition: 1s;
  cursor: pointer;
  width: 350px;
  height: 230px;
  object-fit: contain;
}

.proyectos-links img:hover {
  transform: scale(1.05);
}

.proyecto-link-item {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.53rem;
}

.proyecto-item {
  font-size: 2.3rem;
  padding: 0 1rem;
  color: #000;
}

.proyecto-item:hover {
  color: #4cb8b3;
}

.contacto {
  background-color: #4cb8b3;
  padding: 5rem 0;
  text-align: center;
}
.contacto h3 {
  color: #FFF;
  margin-bottom: 2rem;
}
.contacto .textoContacto {
  color: #FFF;
  font-size: 2rem;
}

.contacto-socialMedia {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.41);
  -moz-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.41);
  box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.41);
  background-color: var(--blanco);
  padding: 2rem;
  border-radius: 2rem;
  width: max-content;
  margin-left: auto;
  margin-right: auto;
}

.contacto-email,
.contacto-celular {
  padding: 1rem;
  display: flex;
  align-items: baseline;
  justify-content: center;
  color: #FFF;
}

.contacto-email-contenido,
.contacto-celular-contenido {
  font-size: 2rem;
}

.icon {
  font-size: 2.1rem;
  padding-right: 0.5rem;
}

.iconos-social {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding-top: 2rem;
}

.icon-contact {
  font-size: 2.5rem;
  color: #FFF;
}

.icon-contact:hover {
  color: #fef46d;
}

.footer {
  background-color: #f53756;
  padding: 1rem;
}
.footer p {
  margin: 0;
  text-align: center;
  color: #FFF;
}